.footer{
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    padding: 3rem  7rem;
    align-items: center;
    position: absolute;
    width: 90rem;
    height: 10rem;
    left: 0;
    top: 62.375rem;
}


@media (max-width:600px){
    .footer{
        width: 23.4375rem;
        height: 12.25rem;
        top: 55.75rem;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        padding: 2rem 0;
    }
}